import { StoreGet, StoreSet } from "@/store"

export interface ModalState {
  createUser: boolean
  editUser: boolean
  createVaccine: boolean
  editVaccine: boolean
  createFeedback: boolean
  [key: string]: boolean
}

export interface ModalActions {
  openModal: (modalName: string) => void
  closeModal: (modalName: string) => void
  closeAllModals: () => void
}

export const initialModal: ModalState = {
  createUser: false,
  editUser: false,
  createVaccine: false,
  editVaccine: false,
  createFeedback: false,
}

export function modalActions(set: StoreSet, get: StoreGet): ModalActions {
  return {
    openModal: (modalName) =>
      set((state) => ({
        modals: {
          ...state.modals,
          [modalName]: true,
        },
      })),
    closeModal: (modalName) =>
      set((state) => ({
        modals: {
          ...state.modals,
          [modalName]: false,
        },
      })),
    closeAllModals: () =>
      set((state) => ({
        modals: Object.keys(state.modals).reduce(
          (acc, key) => {
            acc[key] = false
            return acc
          },
          {} as Record<string, boolean>,
        ),
      }))
  }
}

